.container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    text-align: center;
    padding: 0 16px;
}

.cog1,
.cog2 {
    width: 40vmin;
    height: 40vmin;
    border-radius: 50%;
    color: #f3c623;
    position: relative;
    right: -6vmin;
}

.cog1 {
    animation: rotateLeft 10s linear infinite;
}

.cog2 {
    position: relative;
    left: -6vmin;
    bottom: 0vmin;
    rotate: 30deg;
    color: #4f8a8b;

    animation: rotateRight 10s linear infinite;
}

.first-four,
.second-four {
    position: relative;
    left: 18vmin;
    font-size: 40vmin;
    color: inherit;
}

.second-four {
    left: -18vmin;
}

.wrong-para,
.bottom {
    font-family: 'Montserrat', sans-serif;
    position: absolute;
    bottom: 5vmin;
    padding: 3vmin;
    font-weight: 600;
    animation: opacity 2.5s linear infinite;
}

@keyframes rotateLeft {
    from {
    }
    to {
        transform: rotate(360deg);
    }
}

@keyframes rotateRight {
    from {
    }
    to {
        transform: rotate(-360deg);
    }
}

@keyframes opacity {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

.bottom {
    display: flex;
    align-items: center;
    justify-content: center;
}
